let id = parseInt(window.localStorage.getItem("id") || "0");
let idR = parseInt(window.localStorage.getItem("idV") || "0");
const createId = (): number => {
  id += 1;
  window.localStorage.setItem("id", JSON.stringify(id));
  return id;
};
const createIdR = (): number => {
  idR += 1;
  window.localStorage.setItem("idV", JSON.stringify(idR));
  return idR;
};

export {createId, createIdR};